import React, { Component } from 'react';
import {
    Avatar,
    IconButton,
    Grid,
    Typography
} from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';

class Medal extends Component {
    state = {}
    render() {
        const { medal, country, onAdd, onSubtract } = this.props;
        return (
            <Grid container className="medals" mt={1.5}>

                <Grid item xs={6}>
                    <Typography
                        variant="subtitle1"
                        component="div"
                        sx={{
                            mt: .7,
                            fontWeight: "bold",
                            fontFamily: "Serif"
                        }}
                    >
                        {medal.name} Medals:
                    </Typography>
                </Grid>
                <Grid item xs={6}>
                    <Grid container alignItems="right">

                        <IconButton
                            variant="text"
                            sx={{ color: medal.color }}
                            disabled={country[medal.name] === 0}
                            onClick={() => onSubtract(country.id, medal.name)}
                        >
                            <RemoveCircleIcon />
                        </IconButton>

                        <Avatar
                            sx={{ bgcolor: medal.color, color: "black", mr: -1, ml: -1 }}
                        >
                            {country[medal.name]}
                        </Avatar>

                        <IconButton
                            variant="text"
                            sx={{ color: medal.color }}
                            onClick={() => onAdd(country.id, medal.name)}
                        >
                            <AddCircleIcon />
                        </IconButton>

                    </Grid>

                </Grid>
            </Grid>
        );
    }
}

export default Medal; 