import React, { Component } from 'react';
import Medal from './Medal';
import {
    Card,
    CardContent,
    Divider,
    Grid,
    Typography,
    Badge,
} from '@mui/material';


class Country extends Component {
    getTotals(country, medals) {
        let sum = 0;
        medals.forEach(medal => { sum += country[medal.name]; });
        return sum;
    }

    render() {
        const { country, medals, onAdd, onSubtract } = this.props;
        return (
            <Grid container justifyContent="center">
                <Card
                    className='country'
                    sx={{
                        minWidth: 275,
                        bgcolor: "#404040",
                        color: "white",
                        boxShadow: " 10px 10px 20px 5px black"
                    }}
                >
                    <CardContent>
                        <Typography
                            className="name"
                            gutterBottom
                            variant="h5"
                            component="div"
                            fontFamily="Serif"
                            fontWeight="Bold"
                        >
                            <Badge
                                color="secondary"
                                badgeContent={this.getTotals(country, medals)}
                            >
                                {country.name}
                            </Badge>
                        </Typography>

                        <Divider sx={{ bgcolor: "white" }} />

                        {medals.map(medal =>
                            <Medal
                                key={medal.id}
                                country={country}
                                medal={medal}
                                onAdd={onAdd}
                                onSubtract={onSubtract}
                            />
                        )}

                    </CardContent>
                </Card>
            </Grid>
        );
    }
}

export default Country;