import React, { Component } from "react";
import Country from "./components/Country";
import "./App.css";
import { AppBar, Toolbar, Typography, Badge } from "@mui/material";

class App extends Component {
  state = {
    countries: [
      { id: 1, name: "United States", Gold: 2, Silver: 2, Bronze: 3 },
      { id: 2, name: "China", Gold: 3, Silver: 1, Bronze: 0 },
      { id: 3, name: "Germany", Gold: 0, Silver: 1, Bronze: 2 },
    ],
    medals: [
      { id: 1, name: "Gold", color: "gold" },
      { id: 2, name: "Silver", color: "silver" },
      { id: 3, name: "Bronze", color: "#b08d57" },
    ],
  };

  handleAdd = (countryId, medalName) => {
    const countries = [...this.state.countries];
    const idx = countries.findIndex((c) => c.id === countryId);
    countries[idx][medalName] += 1;
    this.setState({ countries: countries });
  };
  handleSubtract = (countryId, medalName) => {
    const countries = [...this.state.countries];
    const idx = countries.findIndex((c) => c.id === countryId);
    countries[idx][medalName] -= 1;
    this.setState({ countries: countries });
  };
  getTotalMedals() {
    let sum = 0;
    this.state.medals.forEach((medal) => {
      sum += this.state.countries.reduce((a, b) => a + b[medal.name], 0);
    });
    return sum;
  }

  render() {
    return (
      <div>
        <AppBar position="static" sx={{ bgcolor: "#404040" }}>
          <Toolbar variant="dense">
            <Typography
              variant="h6"
              color="inherit"
              component="div"
              fontFamily="Serif"
              fontWeight="Bold"
              fontSize="30px"
              sx={{ mt: 1 }}
            >
              <Badge color="secondary" badgeContent={this.getTotalMedals()}>
                Olympic Medals
              </Badge>
            </Typography>
          </Toolbar>
        </AppBar>

        <div className="countries">
          {this.state.countries.map((country) => (
            <Country
              key={country.id}
              country={country}
              medals={this.state.medals}
              onAdd={this.handleAdd}
              onSubtract={this.handleSubtract}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default App;
